export const renderDate = (input : string) => {
    let array = input.split('');
    let result = '';
    if (input.length === 3) {
        array.splice(2, 0, '/');
        result = array.join('');
        return result;
    } else if (input.length === 6) {
        array.splice(5, 0, '/');
        result = array.join('');
        return result;
    }
}

export const renderDateMobile = (input : string) => {
    let yyyy = input.substr(0, 4);
    let mm = input.substr(5,2);
    let dd = input.substr(8,2);
    let result = dd + "/" + mm + "/" + yyyy;
    console.log(result)
    return result;
}
