import React, { useState, useRef } from 'react'
import styled from 'styled-components'

import color from '../color-css/color'
import { FormData } from './types/FormData'
import { messagesIndexedByNameAttributeWithBooleanValue } from '../dataProcessing'

import TextInput from '../TextInput'
import SuburbAutosuggestField from '../SuburbAutosuggestField'
import WorkPostcode from '../WorkPostcode'
import EmployerAutosuggestField from '../EmployerAutosuggestField'
import ProgressButton, { ButtonStates } from '../ProgressButton'

export type Props = {
    onFormValidatedProp: (isValidated: boolean) => void,
    onClickOnContinueButtonProp: (formData: any) => void
}

const FormStep1Container = styled.div`
    margin-left: 92px;
    margin-top: 168px;
    @media (max-width: 768px) {
        margin-left: 0;
        padding: 15px;
    }
`

const FormStep1Label = styled.h3`
    font-family: museo-sans, sans-serif;
    font-weight: 900;
    font-size: 26px;
    text-align: left;
    letter-spacing: -0.08px;
    color: ${color.blue_17_percent};
`

const FormStep1Form = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 654px;
    height: 100%;
    @media (max-width: 768px) {
        display: block;
        width: 100%;
    }
`

const InputFieldContainer = styled.div`
    margin-top: 1.0625rem;
`

const ProgressButtonPositioningDiv = styled.div`
    margin-top: 2.5rem;
    margin-bottom: 7.625rem;
`

const FormStep1: React.FC<Props> = ({
    onFormValidatedProp,
    onClickOnContinueButtonProp
}) => {
    interface validationByName {
        [label: string]: boolean,
    }

    interface ReactRef {
        [label: string]: React.RefObject<HTMLInputElement>,
    }

    let formData: FormData = {}

    const [selectCantFindSuburb, setSelectCantFindSuburb] = useState(false)
    const [validationInfo, updateValidationInfo] = useState(false)
    const Refs: ReactRef = {
        ["firstName"]: useRef<HTMLInputElement>(null),
        ["lastName"]: useRef<HTMLInputElement>(null),
        ["phoneNumber"]: useRef<HTMLInputElement>(null),
        ["email"]: useRef<HTMLInputElement>(null),
        ["suburbWork"]: useRef<HTMLInputElement>(null),
        ["employer"]: useRef<HTMLInputElement>(null),
    }

    const validation: validationByName = {
        ["firstName"]: false,
        ["lastName"]: false,
        ["phoneNumber"]: false,
        ["email"]: false,
        ["suburbWork"]: false,
        ["employer"]: false,
    }

    const getValidate = () =>
        validation["firstName"]
        && validation["lastName"]
        && validation["phoneNumber"]
        && validation["email"]
        && validation["suburbWork"]
        && validation["employer"]

    const inputFieldFactory = (children: React.ReactNode) => {
        return (
            <InputFieldContainer>
                {children}
            </InputFieldContainer>)
    }

    const createInputField = (
        label: string,
        placeHolderParam: string,
        nameParam: string,
        maxLength?: number,
        aRef?: React.RefObject<HTMLInputElement>): React.ReactNode => {
        return (
            <TextInput
                placeholder={placeHolderParam}
                name={nameParam}
                type="text"
                maxLength={maxLength}
                {...{ label, message: messagesIndexedByNameAttributeWithBooleanValue }}
                onInputSucess={wasAnInputSuccess => {
                    validation[nameParam] = wasAnInputSuccess
                    updateValidationInfo(getValidate())
                }}
                ref={aRef}
            />
        )
    }

    const onFormIsValidated = () => {
        onFormValidatedProp(validationInfo)
        return validationInfo ? ButtonStates.enabled : ButtonStates.disabled
    }

    return (
        <FormStep1Container>
            <FormStep1Label>
                Your work
            </FormStep1Label>
            <FormStep1Form>

                {inputFieldFactory(
                    createInputField("Your first name", "Your first name", "firstName",
                        undefined, Refs["firstName"])
                )}

                {inputFieldFactory(
                    createInputField("Your last name", "Your last name", "lastName",
                        undefined, Refs["lastName"])
                )}

                {inputFieldFactory(
                    createInputField("Your email address", "Your email address", "email",
                        undefined, Refs["email"])
                )}

                {inputFieldFactory(
                    createInputField("Your phone number", "Your phone number", "phoneNumber",
                        12, Refs["phoneNumber"])
                )}

                {inputFieldFactory(
                    <React.Fragment>
                        <SuburbAutosuggestField
                            handleCantFindSuburbProp={selectedCantFindSuburb => {
                                setSelectCantFindSuburb(selectedCantFindSuburb)
                            }}
                            onSelectedItemProp={wasSelected => {
                                validation["suburbWork"] = wasSelected
                                updateValidationInfo(getValidate())
                            }}
                            ref={Refs["suburbWork"]}
                        />
                        <WorkPostcode isShow={selectCantFindSuburb} />
                    </React.Fragment>
                )}

                {inputFieldFactory(
                    <React.Fragment>
                        <EmployerAutosuggestField
                            onInputSucessProp={success => {
                                validation["employer"] = success
                                updateValidationInfo(getValidate())
                            }}
                            ref={Refs["employer"]}
                        />
                    </React.Fragment>
                )}

                <ProgressButtonPositioningDiv>
                    <ProgressButton
                        title="Continue"
                        buttonStates={
                            onFormIsValidated()
                        }
                        onClickCallbackProp={() => {
                            for (const key in Refs) {
                                if (Refs[key].current !== null) {
                                    formData = {
                                        ...formData,
                                        [key]: Refs[key].current?.value
                                    }
                                }
                            }
                            onClickOnContinueButtonProp(formData)
                        }}
                    />
                </ProgressButtonPositioningDiv>
            </FormStep1Form>

        </FormStep1Container >
    )
}

export default FormStep1