
export const messagesIndexedByNameAttributeWithBooleanValue = {
  firstName: { 0: 'Please enter your first name', 1: 'Thanks' },
  lastName: { 0: 'Please enter your last name', 1: 'Thanks' },
  phoneNumber: { 0: 'Phone number has at least 8 number', 1: 'Thanks' },
  email: { 0: 'Please enter a valid email address', 1: 'Thanks' },
  suburbWork: { 0: 'failMessage', 1: 'Thanks' }
}

export const getMessageByName = (name: string, booleanValue: boolean) => {
  const messageByName = name;
  let result = '';
  if (messageByName && typeof messageByName === 'object') {
    result = messageByName[+booleanValue]
  }
  return result;
}